<template>
  <div class="left">
    <div class="left-d">
	  <div class="left-d-d" v-for="(item,index) in list" @click="change_list(index)">{{item.title}}</div>
	  <div class="left-d-d"><router-link to="/Left">Go to Left</router-link></div>
	  <div class="left-d-d" @click="show_note">call alert</div>
    </div>
	<div class="left-d-more" :class="{'left-d-more-show':show}">
	  <router-link v-for="(item,index) in list[c_list].c_title" :to="item.url">
	    <div class="left-d-more-d">{{item.title}}</div>
	  </router-link>
	  <div class="left-d-more-d" @click="show=!show"></div>
	</div>
  </div>
</template>

<script>
import moment from 'moment'
import m from './all.js'
export default {
  data() {
    return {
	  show:false,
	  c_list:0,
	  list:[
	    {title:"",url:"/CaseList/",img:"",no:1,c_title:[
		  {title:"",url:"/Chart1",img:"",no:2},
		  {title:"",url:"/Chart2",img:"",no:3},
		  {title:"",img:"",url:"/Chart3",no:4},
		  {title:"",url:"/Chart4",img:"",no:5},
		  {title:"",url:"/Chart5",img:"",no:6},
		  {title:"",url:"/Chart6",img:"",no:7},
		  {title:"",url:"/Chart7",img:"",no:8},
		]},
		{title:"",url:"/CaseList/",img:"",no:9,c_title:[
		  {title:"",url:"/OrderList/1",img:"",no:10},
		  {title:"",url:"/CaseList/",img:"",no:11},
		  {title:"",url:"/CaseList/",img:"",no:12},
		  {title:"",url:"/CaseList/",img:"",no:13},
		  {title:"",url:"/CaseList/",img:"",no:14},
		]},
		{title:"",url:"/CaseList/",img:"",no:15,c_title:[
		  {title:"",url:"/CaseList/",img:"",no:16},
		  {title:"",url:"/DragDispatch/",img:"",no:17},
		  {title:"-",url:"/TableDispatch/",img:"",no:18},
		]},
		{title:"",url:"/CaseList/",img:"",no:19,c_title:[
		  {title:"",url:"/CaseList/1?key=&pMode=1",img:"",no:20},
		  {title:"",url:"/CaseList/",img:"",no:21},
		  {title:"",url:"/CaseList/",img:"",no:22},
		  {title:"",url:"/CaseList/",img:"",no:23},
		]},
		{title:"",url:"/DriverList/",img:"",no:24,c_title:[
		  {title:"",url:"/DriverList/1?key=&status=",img:"",no:25},
		  {title:"",url:"/CaseList/",img:"",no:26},
		]},
		{title:"",url:"/CarList/",img:"",no:27,c_title:[
		  {title:"",url:"/CarList/1?key=&Status=&carType=0",img:"",no:28},
		  {title:"",url:"/CaseList/",img:"",no:29},
		  {title:"",url:"/CaseList/",img:"",no:30},
		]},
		{title:"",url:"/CaseList/",img:"",no:31,c_title:[
		  {title:"",url:"/CaseList/",img:"",no:32},
		  {title:"",url:"/CaseList/",img:"",no:33},
		  {title:"",url:"/CaseList/",img:"",no:34},
		  {title:"",url:"/CaseList/",img:"",no:35},
		]},
	  ]
	}
  },
  mounted:async function() {
    console.log(this.$route)
  },
  watch:{
    async $route(to, from){
      this.show=false
    }
  },
  methods:{
    get_path:function(x) {
	  var arr=x.split("/")
	  console.log(arr)
	  return arr
	},
	change_list:function(x) {
	  this.show=true
	  this.c_list=x;
	},
    reload:function() {
	  m.reload()
	},
	show_note:function() {
	  var data=moment().format('HH:mm:ss')
      this.$notify({
        title: '',
        dangerouslyUseHTMLString: true,
		duration:10000,
        message: '<div">:TS5555123<br>'+data+'</div>'
      });
	}
  }
}
</script>


